
.address-info-page-Seek {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.address-info-page-Seek .p-ad-form-cli {
  padding: 0.75rem;
  position: relative;
}
.address-info-page-Seek .p-ad-form-cli .addLabel {
  height: 1.25rem;
  line-height: 1.25rem;
  text-align: center;
  margin-bottom: 0.5rem;
}
.address-info-page-Seek .p-ad-form-cli .addLabel span {
  font-size: 0.9rem;
}
.address-info-page-Seek .p-ad-form-cli .m-v-tz:not(:first-child) {
  margin-left: 0.75rem;
  margin-bottom: 0.5rem;
}
.address-info-page-Seek .p-ad-form-cli .m-v-tz {
  min-width: 3.5rem;
  background-color: #f4f4f4;
  border-radius: 4px;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  height: 1.25rem;
  line-height: 1.25rem;
  font-size: 0;
  color: #323232;
}
.address-info-page-Seek .p-ad-form-cli .m-v-tz span {
  font-size: 0.7rem;
}
.address-info-page-Seek .p-ad-form-cli .active {
  background-color: #28cba7 !important;
  color: #fff !important;
}
.address-info-page-Seek .p-ad-form-cli span {
  font-size: 0.7rem;
}
.address-info-page-Seek .login-btn {
  margin: 1.25rem 0.75rem 0 0.75rem;
}
.address-info-page-Seek .login-btn .btn-login-c {
  color: #fff;
  line-height: 0.75rem;
  height: 2rem;
  font-size: 0.75rem;
  border-radius: 0.25rem;
  display: inline-block;
  width: 100%;
}
.address-info-page-Seek .login-btn .login-confirm {
  background-color: #28cba7;
  color: #fff;
}


