
.tab-item {
  font-size: 0;
}
.tab-item .fa-home,
.tab-item .fa-user,
.tab-item .fa-findShome,
.tab-item .fa-fruitCircle {
  width: 1.2rem;
  height: 1.2rem;
}
.tab-item .fa-add {
  width: 2rem;
  height: 2rem;
}
.tab-item i {
  position: relative;
  font-size: 0.75rem;
}
.tab-item i span {
  position: absolute;
  display: inline-block;
  top: -2px;
  left: 56%;
  min-width: 18px;
  text-align: center;
  font-size: 1.2rem;
  color: #fff;
  padding: 3px 4px;
  background-color: #ff4e4e;
  border-radius: 10px;
  white-space: nowrap;
}
.tab-item .tab-label {
  font-size: 0.6rem;
  color: #646464;
  display: block;
}
.tab-item .active {
  color: #28cba7;
}
/* animation */
@-webkit-keyframes icon-bounce {
0%, 100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
25% {
    -webkit-transform: rotate(15deg);
    transform: rotate(15deg);
}
50% {
    -webkit-transform: rotate(-15deg);
    transform: rotate(-15deg);
}
75% {
    -webkit-transform: rotate(5deg);
    transform: rotate(5deg);
}
85% {
    -webkit-transform: rotate(-5deg);
    transform: rotate(-5deg);
}
}
@-webkit-keyframes icon-bounce {
0%, 100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
25% {
    -webkit-transform: rotate(15deg);
    transform: rotate(15deg);
}
50% {
    -webkit-transform: rotate(-15deg);
    transform: rotate(-15deg);
}
75% {
    -webkit-transform: rotate(5deg);
    transform: rotate(5deg);
}
85% {
    -webkit-transform: rotate(-5deg);
    transform: rotate(-5deg);
}
}
@keyframes icon-bounce {
0%, 100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
25% {
    -webkit-transform: rotate(15deg);
    transform: rotate(15deg);
}
50% {
    -webkit-transform: rotate(-15deg);
    transform: rotate(-15deg);
}
75% {
    -webkit-transform: rotate(5deg);
    transform: rotate(5deg);
}
85% {
    -webkit-transform: rotate(-5deg);
    transform: rotate(-5deg);
}
}


