
.minerWindow {
  z-index: 99;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #000;
  width: 100%;
  height: 100%;
  opacity: 0.5;
}
.minerWinContent {
  text-align: center;
  z-index: 999;
  background-color: #fff;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 10px;
  width: 16rem;
}
.minerWinContent .mint-msgbox-header {
  padding: 0.75rem 0 0;
}
.minerWinContent .mint-msgbox-header .mint-msgbox-title {
  text-align: center;
  padding-left: 0;
  margin-bottom: 0;
  font-size: 0.8rem;
  font-weight: 700;
  color: #333;
}
.minerWinContent .mint-msgbox-content {
  padding: 0.5rem 0.5rem 0.75rem;
  border-bottom: 1px solid #ddd;
  min-height: 1.8rem;
  position: relative;
}
.minerWinContent .mint-msgbox-content .mint-msgbox-message {
  color: #333;
  margin: 0;
  text-align: center;
  line-height: 1.8rem;
}
.minerWinContent .mint-msgbox-content .mint-msgbox-input {
  padding-top: 0;
  line-height: 2rem;
}
.minerWinContent .mint-msgbox-content .mint-msgbox-input .mint-msgbox-errormsg {
  color: #f00;
  font-size: 0.6rem;
  min-height: 0.9rem;
  margin-top: 2px;
}
.minerWinContent .mint-msgbox-content .mint-msgbox-input input {
  border: 1px solid #dedede;
  border-radius: 5px;
  padding: 4px 5px;
  width: 60%;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
}
.minerWinContent .mint-msgbox-content .mint-msgbox-input .qgip {
  border: 1px solid #dedede;
  border-radius: 5px;
  padding: 4px 5px;
  width: 25%;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
}
.minerWinContent .mint-msgbox-btns {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 2rem;
  line-height: 2rem;
}
.minerWinContent .mint-msgbox-btns .mint-msgbox-btn {
  line-height: 1.75rem;
  display: block;
  background-color: #fff;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  margin: 0;
  border: 0;
}
.minerWinContent .mint-msgbox-btns .mint-msgbox-confirm {
  color: #26a2ff;
  width: 50%;
}
.minerWinContent .mint-msgbox-btns .msgbox-cancel {
  border-right: 1px solid #ddd !important;
}


