
.top-bar {
  position: relative;
  background-color: #fff;
  position: relative;
  z-index: 100;
}
.top-bar:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.top-bar .top-title {
  height: 42px;
  width: 100%;
  font-size: 0;
  text-align: center;
  background-color: #f4f4f4;
}
.top-bar .top-title .text-title {
  line-height: 42px;
  font-size: 16px;
  vertical-align: top;
}
.top-bar .btn-back {
  position: absolute;
  top: 0;
  left: 0;
  width: 42px;
  height: 42px;
  line-height: 42px;
  text-align: center;
  z-index: 99;
}
.top-bar .btn-back span {
  font-size: 0.8rem;
}
.top-bar .btn-back .icon-arrow_lift {
  font-size: 20px;
  color: #28cba7;
}
.top-bar .btn-back img {
  width: 0.55rem;
  height: 0.975rem;
  vertical-align: middle;
}
.top-bar .btn-menu {
  position: absolute;
  top: 0;
  right: 0;
  height: 42px;
  line-height: 42px;
  color: #28cba7;
  margin-right: 10px;
}


