
.minerWindow {
  z-index: 99;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #000;
  width: 100%;
  height: 100%;
  opacity: 0.5;
}
.minerWinContent {
  text-align: center;
  z-index: 999;
  background-color: #fff;
  padding: 0.68266667rem 1.152rem 1.92rem;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 0.32rem;
  height: 25rem;
  width: 15rem;
  overflow-y: auto;
}
.minerWinContent .close {
  font-size: 1.2rem !important;
  color: #28cba7;
  width: 0.768rem;
  height: 0.768rem;
  position: fixed;
  top: 0.68266667rem;
  right: 0.68266667rem;
}
.minerWinContent .minerTit span {
  margin-left: 0.42666667rem;
}
.minerWinContent .minerTitMoney span {
  position: absolute;
  right: 0;
  color: #000;
}
.minerWinContent .remark span {
  margin-right: 0.29866667rem;
  color: #ffcb3c;
}
.minerWinContent .applyBtn {
  position: absolute;
  left: 0;
  padding: 0.55466667rem 0;
  width: 100%;
  font-size: 0.704rem !important;
  background-color: #28cba7;
  color: #fff;
  display: inline-block;
  white-space: nowrap;
  letter-spacing: 0.08533333rem;
}


