
.input-container {
  width: 100%;
  position: absolute;
  bottom: 0;
  background-color: #fff;
}
.input-container .picker-toolbar {
  border-bottom: 1px solid #eaeaea;
  height: 2rem;
}
.input-container .picker-toolbar .pick-cancle {
  float: left;
  display: inline-block;
  width: 50%;
  text-align: center;
  line-height: 2rem;
  font-size: 0.8rem;
  color: #26a2ff;
}
.input-container .picker-toolbar .pick-confirm {
  float: right;
  display: inline-block;
  width: 50%;
  text-align: center;
  line-height: 2rem;
  font-size: 0.8rem;
  color: #26a2ff;
}
.input-container .input-area-aur {
  position: relative;
  text-align: left;
  font-size: 0.8rem;
  padding: 0.5rem;
  color: #ccc;
  height: 6rem;
}
.input-container .input-area-aur .aur-line {
  text-align: center;
  margin-top: 1.5rem;
}
.input-container .input-area-aur .aur-line .s-a-box-db {
  width: 3rem;
  border: 1px solid #ccd0d1;
  border-radius: 4px;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  height: 1rem;
  line-height: 1rem;
  font-size: 0;
  color: #999;
}
.input-container .input-area-aur .aur-line .visited {
  background-color: #28cba7;
  color: #fff !important;
}
.input-container .input-area-aur .aur-line span {
  font-size: 0.8rem;
}
.input-container .input-area-aur .aur-line .s-a-box-db:not(:first-child) {
  margin-left: 0.5rem;
}


