
.billSel-info-page {
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  font-size: 0.75rem;
}
.billSel-info-page h6 {
  margin: 0.25rem 0 0.25rem 1rem;
  font-size: 1rem;
}
.billSel-info-page .ul-goods-list .clearfix .s-a-box-db-long {
  min-width: 2.5rem;
  border: 1px solid #ccd0d1;
  border-radius: 4px;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  height: 1.25rem;
  line-height: 1.25rem;
  color: #999;
  padding: 0.05rem;
  margin: 0.25rem 0 0.25rem 1rem;
}
.billSel-info-page .ul-goods-list .clearfix .s-a-box-db {
  min-width: 3.25rem;
  border: 1px solid #ccd0d1;
  border-radius: 4px;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  height: 1.25rem;
  line-height: 1.25rem;
  color: #999;
  padding: 0.05rem;
  margin: 0.25rem 0 0.25rem 1rem;
}
.billSel-info-page .ul-goods-list .clearfix .s-a-box-db span {
  font-size: 0.75rem;
}
.billSel-info-page .ul-goods-list .clearfix .visited {
  background-color: #28cba7;
  color: #fff !important;
}
.billSel-info-page .login-btn {
  margin: 1.75rem 0.75rem 0 0.75rem;
}
.billSel-info-page .login-btn .btn-login-c {
  color: #fff;
  font-size: 0.75rem;
  height: 2rem;
  border-radius: 5px;
  display: inline-block;
  width: 100%;
}
.billSel-info-page .login-btn .login-confirm {
  background-color: #28cba7;
  color: #fff;
  border: 1px solid #0bbe06;
}


