
.upload-view-info-page {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.upload-view-info-page .m-u-v-total {
  height: 1.5rem;
  line-height: 1.5rem;
  border-bottom: 1px solid #d7d7d7;
  padding: 5px;
  text-align: center;
  color: #28cba7;
}
.upload-view-info-page .m-u-v-form-cli {
  position: relative;
  border-bottom: 1px solid #d7d7d7;
}
.upload-view-info-page .m-u-v-form-cli .m-u-v-table {
  padding: 0 10px 10px 10px;
  border-bottom: 1px solid #d7d7d7;
}
.upload-view-info-page .m-u-v-form-cli .m-u-v-table .m-u-v-cell {
  margin-top: 10px;
}
.upload-view-info-page .m-u-v-form-cli .m-u-v-content {
  padding: 10px 10px 10px 10px;
  font-size: 0.7rem;
}
.upload-view-info-page .m-u-v-form-cli .m-u-v-content span:not(:first-child) {
  margin-left: 0.6rem;
}
.upload-view-info-page .m-u-v-form-cli .m-u-v-content .con-cell {
  color: #28cba7;
}


