
.message-info-page {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.message-info-page .m-v-form-cli {
  padding: 0.5rem 0.75rem 0.5rem 0.75rem;
  position: relative;
  border-bottom: 1px solid #d7d7d7;
  font-size: 0;
}
.message-info-page .m-v-form-cli img {
  float: right;
  margin-top: 0.5rem;
}
.message-info-page .m-v-form-cli .m-v-tz {
  width: 3rem;
  background-color: #929292;
  border-radius: 4px;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  height: 1rem;
  line-height: 1rem;
  font-size: 0;
  color: #fff;
}
.message-info-page .m-v-form-cli .m-v-tz span {
  font-size: 0.8rem;
}
.message-info-page .m-v-form-cli .active {
  background-color: #00c850 !important;
}
.message-info-page .m-v-form-cli .m-v-content {
  font-size: 0.8rem;
  margin: 0.5rem 0 0.5rem 0;
  line-height: 1.25rem;
}
.message-info-page .m-v-form-cli .m-v-time {
  color: #929292;
  height: 1rem;
}
.message-info-page .m-v-form-cli .m-v-time span {
  font-size: 0.7rem;
}


